.SideDrawer{
    position: fixed;
    width:280px;
    max-width: 70%;
    height:100%;
    left:0;
    top:0;
    background-color: white;
    z-index: 200;
    transition: transform 0.3s ease-out;
    padding: 32px 16px;
    box-sizing: border-box;
}

@media (min-width: 500px){
    .SideDrawer{
        display:none;
    }
}

.Open{
    transform: translateX(0);
}

.Close{
    transform: translateX(-100%);
}

.Logo-SideDrawer{
    height: 11%;
    margin-bottom: 32px;
}